.sidebar {
    padding: 1rem;
    padding-top: 4rem;
    /* if banner present use 7 rem padding on top */
    padding-top: 7rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: fixed;
    z-index: 10;
    top: 0;
    left: -20rem;
    width: 20rem;
    height: 100vh;
    background-color: var(--background-color);
    border-right: 1px solid var(--accent-color);
    transition: left 0.2s ease-in-out;

    .section {
        gap: 1rem;
        display: flex !important;
        flex-direction: column;
    }
}

.sidebar.open {
    left: 0;
}
