.social-card {
    border-radius: 1rem;
    box-shadow: 0px 0px 10px var(--background-alt-color);
    padding: 1rem;
    color: #fff;
    width: 10rem;
    height: 15rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 0.8rem;
    justify-content: center;
    gap: 2rem;
    padding-bottom: 2rem;
    transition: 0.1s ease-in-out;
    cursor: pointer;
    background-color: var(--background-color);
}

.social-card:hover {
    transform: scale(1.1);
}

.social-card-image {
    object-fit: contain;
    width: 100%;
    aspect-ratio: 1/1;
}
